.main.section .info {
  opacity: 0;
  padding: 10px;
  position: absolute;
  top: calc(50% + 3.5rem);
  left: 50%;
  transform: translate(-50%, -50%); }
  @media (max-width: 768px) {
    .main.section .info {
      width: 95vw; } }
  .main.section .info a.button {
    display: block;
    padding: 30px;
    background-color: #ff6857;
    width: fit-content;
    margin: 0 auto;
    margin-top: 7rem;
    box-shadow: 3px 3px 0 #ff4733;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    font-weight: 600;
    color: white;
    text-transform: uppercase; }
  .main.section .info h1 {
    font-family: 'Merriweather', sans-serif;
    font-size: 110px;
    text-shadow: 3px 2px 0 black;
    color: white;
    white-space: nowrap;
    text-align: center; }
    @media (max-width: 768px) {
      .main.section .info h1 {
        font-size: 64px;
        white-space: normal; } }
  .main.section .info .grid {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    filter: grayscale(0.9); }
    @media (max-width: 768px) {
      .main.section .info .grid {
        flex-wrap: wrap; } }
    .main.section .info .grid .grid__item {
      display: flex;
      flex-basis: auto;
      padding: 5px; }
      @media (max-width: 768px) {
        .main.section .info .grid .grid__item {
          flex-basis: 16.667%; } }
      .main.section .info .grid .grid__item img {
        width: 100%;
        height: auto;
        max-width: 150px; }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.contact.section {
